#waitingForClass, #waiting, #main-content-home {
	width: 100%;
	height: 100vh;
}

.grid-main-content-home {
	display: grid;
	grid-template-columns: 20% 80%;
	grid-template-rows: 1;
}

.flex-main-content-home {
	display: flex;
	justify-content: center;
	align-items: center;
}

#main-first-grid-container {
	width: 100%;
	height: 100vh;
	overflow: hidden;
	animation-name: fadeOut;
	animation-duration: 1.5s;
}

@keyframes fadeIn {
	from {
		transform: translateX(0);
	}
	to {
		transform: translateX(-100%);
	}	
}

@keyframes fadeOut {
	from {
		transform: translateX(-100%);
	}
	to {
		transform: translateX(0);
	}	
}

#schedule-home {
	width: 100%;
	height: 100%;
	overflow-y: scroll;
	border-top: 1px solid orange;
}

.first-grid-item-container {
	width: 100%;
	min-height: 10vh;
	background: rgba(249, 112, 71, 1);
	backdrop-filter: blur(1px);
	border-bottom: 1px dashed;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}


#schedule-home::-webkit-scrollbar {
	width: 0.5rem;
	background: rgba(255, 255, 255, 0.4);
}

#schedule-home::-webkit-scrollbar-button {
	background: orange;
}

#schedule-home::-webkit-scrollbar-thumb {
	background: rgba(0, 0, 0, 1);
}

#h1-title-home {
	height: 10vh;
	display: flex;
	justify-content: center;
	align-items: center;
	background: rgba(255, 255, 255, 0.6);
}

#schedule-choice {
	font-size: 2.2rem;
	width: 100%;
	border: none;
	background: transparent;
}

.first-grid-item-container h3 {
	width: 100%;
	font-size: 1.3rem;
	padding-bottom: 0.5rem;
	padding-top: 0.2rem;
	cursor: pointer;
	text-transform: uppercase;
	letter-spacing: 0.1rem;
}

.first-grid-item-container h3::after {
	content: '<>';
	position: absolute;
	bottom: 0;
	right: 0;
}

.first-grid-item-container p {
	width: 100%;
	font-size: 1.5rem;
	letter-spacing: 0.1rem;
	padding: 0.1rem 0;
	background: rgba(255, 255, 255, 0.55);
}

/*----------------------------------------------*/

#main-second-grid-container {
	width: 100%;
	height: 100vh;
	display: grid;
	grid-template-columns: 1;
	grid-template-rows: 5% 3% 88%;
}

.h3-home-second-grid {
	padding-right: 0.5rem;
}

.p-home-second-grid {
	padding-right: 0.5rem;
	font-size: 1.5rem;
	font-weight: bolder;
	color: orange;
}

.h4-home-second-grid {
	padding-right: 0.5rem;
}

.main-home-second-grid {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.h2-home-second-grid {
	width: 85%;
	padding-right: 0.5rem;
	backdrop-filter: blur(5px);
	color: #fefffb;
}

/*-----------------------------------------*/

#absolute-toggle-exercise-display {
	width: 20%;
	position: relative;
	top: 0;
	left: 0;
	z-index: 2;
	text-align: right;
	display: flex;
	justify-content: flex-start;
}

#arrow-container {
	font-size: 1rem;
	text-align: left;
	border-radius: 0px 8px 8px 0px;
	box-shadow: 0px 1px 2px orange;
	color: orange;
	background: black;
	overflow: hidden;
}

#toggle-button-exercise-display {
	padding: 0.3rem 0.6rem;
	cursor: pointer;
	font-size: 1.7rem;
}

/* mobile view */

@media only screen and (max-width: 1300px) {

	.grid-main-content-home {
		grid-template-columns: 30% 70%;
	}

}

@media only screen and (max-width: 975px) {

	.grid-main-content-home {
		grid-template-columns: 35% 65%;
	}

	#schedule-choice, #schedule-choice > option {
		font-size: 1.9rem;
	}

	.first-grid-item-container h3 {
		font-size: 1.1rem;
	}

	.first-grid-item-container p {
		font-size: 1.2rem;
	}

}

@media only screen and (max-width: 725px) {

	#main-first-grid-container {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;
		height: 100%;
	}

	.grid-main-content-home {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	#schedule-home {
		height: 100%;
	}

	#main-second-grid-container {
		height: 100%;
		flex-direction: column;
	}

	.same-line {
		justify-content: center !important;
	}

	.h3-home-second-grid {
		font-size: 1rem;
	}

}

