body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
}

a {
  text-decoration: none;
  color: black;
}

:root {
  --blue: #5d73e1;
  --pink: rgba(211,120,135, 0.5);
}

#pink-background {
    background: var(--pink);
}

#blue-background {
  background: var(--blue);
}

.App {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.grid {
  display: grid;
}

.flex {
  display: flex;
}

.full-width {
  width: 100%;
}

.textalign-left {
  width: 80%;
  text-align: left;
}

.highlight {
  color: #c943eb;
}

.ENF {
  text-transform: uppercase;
  font-weight: bolder;
}

.no-display {
  display: none;
}

.same-line {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: row;
}

