.animationStartLeft {
  animation-name: startLeft;
  animation-duration: 1.5s;
}
@keyframes startLeft {
  from {
    transform: translateX(-100%);
  } to {
    display: block;
  }
}

.animationStartRight {
  animation-name: startRight;
  animation-duration: 1.5s;
}
@keyframes startRight {
  from {
    transform: translateX(100%);
  } to {
    display: block;
  }
}

.main-background-woman {
  width: 100%; 
  height: 100%;
  background: url(../img/woman-background.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top;
}

.main-background-man {
  width: 100%;
  height: 100%;
  background: url(../img/man-background.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: right top;
}

.background-woman {
  width: 100%;
  height: 100vh;
  position: absolute;
  background: var(--pink);
}

.background-man {
  width: 100%;
  height: 100vh;
  position: absolute;
  background: rgba(26,35,76, 0.5);
}

#login-container {
	width: 30vw;
	height: 50vh;
	border: 1px solid grey;
	border-radius: 6px;
	overflow: hidden;
}

.woman-login-container {
	background: #ffedf0;
	animation-name: hideSee;
	animation-duration: 2.5s;
}

.man-login-container {
	background: #a7d1e5;
	animation-name: hideSee;
	animation-duration: 2.5s;
}

@keyframes hideSee {
	from {
		visibility: hidden;
	} 50% {
		visibility: hidden;
		transform: translateY(-100%);
	} to {
		visibility: visible;
	}
}



.left-man {
	margin-left: auto;
	margin-right: 10%;
	margin-top: 5%;
}

.right-woman {
	margin-right: auto;
	margin-left: 10%;
	margin-top: 5%;
}

.logo-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 40%;
}

.logo-container h6 {
	font-size: 1.8rem;
	font-weight: lighter;
}

.logo-container p {
	padding-top: 1.2rem;
	font-size: 1rem;
}

.body-container {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	height: 40%;
}

.email-container {
	width: 100%;
	padding: 0.4rem 0;
}

.password-container {
	width: 100%;
	padding: 0.4rem 0;
}

.input {
	width: 80%;
	border-radius: 4px;
	border: 1px solid grey;
	font-size: 1.2rem;
	padding: 0.6rem 0.2rem;
	text-align: left;
}

.checkbox-container {
	width: 80%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
}

#password-checkbox {
	position: relative;
	height: 20px;
	width: 20px;
	margin-right: 0.2rem;
}

input[type="checkbox"]:checked {
	color: red;
}

#password-checkbox:hover::after {
	content: '';
	position: absolute;
	top: -50% ;
	left: -50% ;
	width: 40px;
	height: 40px;
	background: rgba(244, 77, 255, 0.2);
	border-radius: 50%;
}

.footer-container {
	grid-template-columns: 1fr 1fr;
}

.footer-container div {
	align-items: flex-start;
}

.P1-container {
	justify-content: flex-start;
}
.P1 {
	width: 80%;
}

.P2-container {
	justify-content: flex-end;
	padding-right: 10%;
}

.P2 {
	width: fit-content;
	border: 1px solid grey;
	background: rgba(142, 61, 203, 0.29);
	font-size: 1.5rem;
	padding: 0.3rem 0.9rem;
	color: black;
	border-radius: 10px;
	text-align: right;
	cursor: pointer;
}


/* mobile view */
@media only screen and (max-width: 1400px) {

	#login-container {
		width: 40%;
	}

}

@media only screen and (max-width: 1000px) {

	#login-container {
		width: 50%;
	}

}

@media only screen and (max-width: 700px) {

	#login-container{
		width: 60%;
	}
	.left-man {	
		margin-right: 20%;
		margin-top: 5%;
	}

	.right-woman {
		margin-left: 20%;
		margin-top: 5%;
	}
	.input {
		font-size: 1rem;
	}
}